* {
    margin: 0;
    padding: 0;
}

.container {
    background: black;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    

}

.app-wrapper{
    background-color: aquamarine;
    min-width: 450px;
    min-height: 450px;
    padding: 32px;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 4px 6px 42px wheat;
    margin-bottom: 12px;


}

.header h1{
    color: aliceblue;
    text-align: center;
    margin: 30px 0;

}

.task-input {
    outline: none;
    width: 260px;
    padding: 14px;
    margin-right: 25px;
    font-size: 20px;
    color: rgb(232, 6, 37);
    
}